.padding-sides {
  &-0 {
    padding-left:0  !important;
    padding-right: 0 !important;
  }
  &-15 {
    padding-left:15px !important;
    padding-right: 15px !important;
  }
}

tbody .padding-sides {
  &-15 {
    position: relative;
    &:after{
      content: '';
      position: absolute;
      height: 100%;
      background: rgb(2,0,36);
      background: linear-gradient(90deg, rgba(2,0,36,0) 0%, rgba(46,50,59,1) 100%);
      width: 15px;
      right: 0;
      top: 0;
    }
  }
}
tbody tr {
  &:hover {
    .padding-sides {
      &-15 {
        &:after {
          background: rgb(2,0,36);
          background: linear-gradient(90deg, rgba(2,0,36,0) 0%, rgba(54,58,67,1) 100%);
        }
      }
    }
  }
  &.active {
    .padding-sides {
      &-15 {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          height: 100%;
          background: rgb(2, 0, 36);
          background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, rgba(54, 62, 82, 1) 100%);
          width: 15px;
          right: 0;
          top: 0;
        }
      }
    }
    &:hover {
      .padding-sides {
        &-15 {
          &:after {
            background: rgb(2, 0, 36);
            background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, rgba(60, 66, 86, 1) 100%);
          }
        }
      }
    }
  }
}


.configTable {
  tbody {
    tr {
      cursor:pointer;
      &:hover {
        background: rgba(255, 255, 255, 0.04);
      }
      &.active {
        background: rgba(98, 121, 195, 0.17);
        &:hover {
          background: rgba( 60, 66, 86, 1);
        }
      }
    }
  }
}
.table_action_bar {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #434753;
  box-shadow: 0px -3px 5px rgba(12, 13, 15, 0.2), 0px -1px 18px rgba(12, 13, 15, 0.12), 0px -6px 10px rgba(12, 13, 15, 0.14);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items:center;
  padding: 0 20px;
  height: 64px;
  &>div:nth-of-type(1) {
    margin-right: auto;
  }
  div {
    display: inline-flex;
    width: auto;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
  }
  .btn {
    padding-left: 20px;
    padding-right: 20px;
    font-size: 14px;
    letter-spacing: 1.25px;
    &--transparent {
      border: 2px solid #C1C4CD;
      color: rgba(255,255,255,0.86);
    }
  }
  &__info {
    margin-right: 20px;
    color: white;
    b{
      margin-left: 5px;
      color: white;
    }
  }
}

.icon-inside {
  svg {
    margin-right: 8px;
  }
}