// ==================== BASIC RULES ========================= //

*, body, html {
  -webkit-tap-highlight-color: transparent;
}

h1 {
  margin: 20px 0 20px 0;
}

h2 {
  letter-spacing: 1px;
  margin-bottom: 32px;
}

p {
  margin: 0 0 20px 0;
}

h1, h2, h3, h4, h5, h6, p, span, li, a {
  &::selection {
    //color: $black; /* Цвет текста */
    //background: $white; /* Цвет фона */
  }
}

ol, ul {
  padding-left: 40px;
}
.grey-line {
  width: 100%;
  height: 1px;
  background:  rgba(255, 255, 255, 0.12);
}

// ==================== BASIC RULES END ========================= //


.popup-layer {
  background: rgba(22,24,28,0.8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items:center;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  .popup-content  {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items:center;
    width: 320px;
    margin: auto;
    border-radius: 8px;
    box-shadow: 0px 11px 15px rgba(12, 13, 15, 0.2), 0px 9px 46px rgba(12, 13, 15, 0.12), 0px 24px 38px rgba(12, 13, 15, 0.14);
    header {
      background: #434753;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      height: 56px;
      width: 100%;
      padding: 0 0 0 20px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items:center;
      h3 {
        font-size: 14px;
        line-height: 19px;
        font-weight: 500;
        color: rgba(255, 255, 255, 0.87);
        margin: 0;
        height: 1;
      }
      .close {
        cursor:pointer;
        transition-duration:.2s;
        width: 56px;
        height: 56px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items:center;
        border-top-right-radius: 8px;
        &:hover {
          transition-duration:.2s;
          background: rgba(255, 255, 255, 0.05);

        }
      }
    }
    main {
      width: 100%;
      background: #2C2F38;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      padding: 15px 20px;
      .header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items:center;
        div {
          width: 50%;
          h6{
            margin: 0;
          }
          p, span, h6 {
            font-size: 13px;
            font-weight: 300;
          }
        }
      }
      .MuiFormControl-root{
        margin-bottom: 15px;
      }
      p {
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.4px;
      }
    }
  }
}


// ==================== BASIC RULES END ========================= //